import {
  getElementSize,
  getRotationMatrix,
} from '../../../../../utils/index.js';
import { GLTFLoader } from 'three/addons/loaders/GLTFLoader.js';
import * as THREE from 'three';

export default class GsRenderController {
  constructor(sceneController) {
    this.viewer = null;
    this.splatConfigs = [];
    this.sceneController = sceneController;
    this.renderer = this.sceneController.getRenderer();
    this.camera = this.sceneController.getCamera();
    this.scene = this.sceneController.getScene();
  }

  async render(config, container, wrapper = undefined) {
  }

  animate() {
  }

  onWindowResize() {
  }

  dispose() {
  }

  async loadMesh(splatConfig) {
    console.log('loadMesh', splatConfig);
    // this.createFloor({
    //   size: [1000, 1000],
    //   position: [0, 0, 0],
    //   rotation: [-90, 0, 8],
    //   color: '#ffffff',
    //   opacity: 1,
    //   isCollision: true,
    //   visible: false,
    // });
    if (splatConfig.meshName) {
      await this.loadSceneMesh(splatConfig.meshName, splatConfig.scale);
    } else this.createFloor({
      size: [1000, 1000],
      position: [0, 0, 0],
      rotation: [-90, 0, 8],
      color: '#ffffff',
      opacity: 1,
      isCollision: true,
      visible: false,
    });
    if (splatConfig.customMesh?.length !== 0) {
      splatConfig.customMesh?.forEach((mesh) => {
        this.createFloor(mesh);
      });
    }
  }

  loadSceneMesh(meshPath, scale) {
    console.log('addMesh', meshPath, 'scale', scale);
    if (!meshPath) {
      console.warn('meshName is not exist');
      return Promise.reject('mesh name is not exist');
    }
    return new Promise((resolve, reject) => {
      const that = this;
      const loader = new GLTFLoader();
      if (!meshPath.startsWith('http')) {
        loader.setPath('./models/');
      }
      loader.load(meshPath, (gltf) => {
        const collisionWorld = gltf.scene;
        collisionWorld.rotation.y = Math.PI;

        // è®¾ç½®æ¨¡å‹ç¼©æ”¾
        if (scale && Array.isArray(scale) && scale.length === 3) {
          collisionWorld.scale.set(scale[0], scale[1], scale[2]);
          console.log('è®¾ç½®æ¨¡å‹ç¼©æ”¾:', scale);
        }

        collisionWorld.traverse((child) => {
          if (child.isMesh) {
            child.castShadow = true;
            child.receiveShadow = true;
          }
        });

        that.sceneController.getScene()
          .add(collisionWorld);
        that.debugMesh = collisionWorld;
        that.sceneController.getWorldOctree()
          .fromGraphNode(that.debugMesh);
        return resolve();
      });
    });
  }

  createFloor(floorConfig) {
    console.log('createFloor', floorConfig);
    // åˆ›å»ºåœ°æ¿å‡ ä½•ä½“
    const floorGeometry = new THREE.PlaneGeometry(
      floorConfig.size[0],
      floorConfig.size[1],
    );

    // åˆ›å»ºåœ°æ¿æè´¨
    const floorMaterial = new THREE.MeshLambertMaterial({
      color: new THREE.Color(floorConfig.color),
      transparent: true,
      opacity: floorConfig.opacity,
    });

    // åˆ›å»ºåœ°æ¿ç½‘æ ¼
    const floor = new THREE.Mesh(floorGeometry, floorMaterial);
    floor.rotation.x = (floorConfig.rotation[0] * Math.PI) / 180; // æ—‹è½¬90åº¦ä½¿å…¶æ°´å¹³
    floor.rotation.y = (floorConfig.rotation[1] * Math.PI) / 180; // æ—‹è½¬90åº¦ä½¿å…¶æ°´å¹³
    floor.rotation.z = (floorConfig.rotation[2] * Math.PI) / 180; // æ—‹è½¬90åº¦ä½¿å…¶æ°´å¹³
    // floor.rotation.x = -Math.PI / 2; // æ—‹è½¬90åº¦ä½¿å…¶æ°´å¹³
    // eslint-disable-next-line prefer-destructuring
    floor.position.x = floorConfig.position[0]; // è®¾ç½®åœ¨y=0çš„ä½ç½®
    // eslint-disable-next-line prefer-destructuring
    floor.position.y = floorConfig.position[1]; // è®¾ç½®åœ¨y=0çš„ä½ç½®
    // eslint-disable-next-line prefer-destructuring
    floor.position.z = floorConfig.position[2]; // è®¾ç½®åœ¨y=0çš„ä½ç½®
    if (floorConfig.isCollision) {
      this.sceneController.getWorldOctree()
        .fromGraphNode(floor);
      floor.receiveShadow = true; // æ¥æ”¶é˜´å½±
    }
    floor.visible = floorConfig.visible ? floorConfig.visible : false;

    // æ·»åŠ åˆ°åœºæ™¯
    this.scene.add(floor);
  }

  setInfoPanelVisible(visible) {
  }

  getConfig(config) {
    let splatConfigs = [];
    if (config.url instanceof Array) {
      splatConfigs = config.url.map(item => ({
        url: item.path,
        show: false,
        position: item.position || config?.splashInfo?.position || [0, 0, 0],
        rotation: getRotationMatrix(item?.rotation) || [0, 0, -1, 0],
        scale: item.scale || config?.splashInfo?.scale || [1, 1, 1],
        meshName: config?.splashInfo?.meshName,
        rendererScale: config?.rendererScale || 1,
        customMesh: config?.splashInfo?.customMesh || [],
      }));
    } else {
      splatConfigs = [
        {
          url: config.url,
          show: false,
          position: config?.splashInfo?.position || [0, 0, 0],
          rotation: getRotationMatrix(config?.splashInfo?.rotation) || [
            0, 0, -1, 0,
          ],
          scale: config?.splashInfo?.scale || [1, 1, 1],
          meshName: config?.splashInfo?.meshName,
          rendererScale: config?.rendererScale || 1,
          customMesh: config?.splashInfo?.customMesh || [],
        },
      ];
    }
    return splatConfigs;
  }

  setRenderScale(container, rendererScale, wrapper) {
    container.style.transform = `scale(${1 / rendererScale})`;
    container.style.transformOrigin = '0 0';
    const size = getElementSize(wrapper || document.body);
    const width = size.width * rendererScale;
    const height = size.height * rendererScale;
    this.renderer.setSize(width, height);
  }
}
