import Stats from 'three/addons/libs/stats.module.js';

export class StatsModule {
  constructor(option) {
    if (!option?.containerId) {
      throw new Error('containerId is required');
    }
    this.init(option);
  }

  init(option) {
    const container = document.getElementById(option.containerId);
    this.stats = new Stats();
    this.stats.showPanel(option?.type || 0);
    option?.style?.position
      && (this.stats.dom.style.position = option?.style?.position || 'absolute');
    option?.style?.top && (this.stats.dom.style.top = option?.style?.top);
    option?.style?.left && (this.stats.dom.style.left = option?.style?.left);
    option?.style?.right && (this.stats.dom.style.right = option?.style?.right);
    option?.style?.bottom
      && (this.stats.dom.style.bottom = option?.style?.bottom);
    this.stats.dom.style.display = 'none';
    container.appendChild(this.stats.dom);
    console.log(this.stats.dom, option);
  }

  hide() {
    this.stats.dom.style.display = 'none';
  }
  show() {
    this.stats.dom.style.display = 'block';
  }
}
