export function getElementSize(element) {
  return {
    width: element.clientWidth,
    height: element.clientHeight,
  };
}

export function isWeixin() {
  console.log('isWeixin===', navigator.userAgent);
  return /micromessenger/i.test(navigator.userAgent);
}

export function loadWxJsSdk() {
  return new Promise((resolve, reject) => {
    if (window.wx) return resolve(window.wx);
    const script = document.createElement('script');
    script.type = 'text/javascript';
    script.src = './jweixin.js';
    script.async = true;
    script.onload = () => resolve(window.wx);
    script.onerror = err => reject(err);
    document.head.appendChild(script);
  });
}

/**
 * å°†è§’åº¦è½¬æ¢ä¸ºå¼§åº¦ï¼Œç”¨äºGLTFçš„rotationå€¼
 * @param {Array} degrees - è§’åº¦æ•°ç»„ [x, y, z]ï¼Œä¾‹å¦‚ [0, 180, 0]
 * @returns {Array} å¼§åº¦æ•°ç»„ [x, y, z]ï¼Œä¾‹å¦‚ [0, Math.PI, 0]
 */
export function degreesToRadians(degrees) {
  if (!Array.isArray(degrees) || degrees.length !== 3) {
    console.warn('degreesToRadians: å‚æ•°å¿…é¡»æ˜¯åŒ…å«3ä¸ªå…ƒç´ çš„æ•°ç»„');
    return [0, 0, 0];
  }

  return degrees.map(degree => THREE.MathUtils.degToRad(degree));
}
