import { getElementSize } from '../../utils/index.js';

export const sizes = {
  width: window.innerWidth,
  height: window.innerHeight,
};
// import { sizes } from "./sizes.js";

let camera;
let renderer;
let resizeObserver;
const listenEvent = () => {};

function resizeEvent(container) {
  // Update sizes
  const { width, height } = getElementSize(container || document.body);

  // Update camera
  camera.aspect = width / height;
  camera.updateProjectionMatrix();

  // Update renderer
  renderer.setSize(width, height);
  renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
}

export function resizeEventListener(
  _camera,
  _renderer,
  container,
  renderScale = 1,
) {
  camera = _camera;
  renderer = _renderer;

  resizeObserver = new ResizeObserver(() => {
    // Update sizes
    const size = getElementSize(document.body);
    console.log('resize', size, container);
    // Update camera
    camera.aspect = size.width / size.height;
    camera.updateProjectionMatrix();

    container.style.transform = `scale(${1 / renderScale})`;
    const width = size.width * renderScale;
    const height = size.height * renderScale;
    renderer.setSize(width, height);
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
  });
  resizeObserver.observe(document.body);
}

export function clear() {
  resizeObserver.unobserve(document.body);
}
