import * as GaussianSplats3D from '@mkkellogg/gaussian-splats-3d';
import { resizeEventListener } from '../../../../utils/window.js';
import GsRenderController from './gs-render-controller.js';

export class GS3dRenderController extends GsRenderController {
  async render(config, container, wrapper = undefined) {
    console.log(
      'init3DGS using GaussianSplats3D render ',
      config,
      container,
      wrapper,
    );

    const splatConfigs = this.getConfig(config);
    console.log('splatConfigs', splatConfigs);
    this.splatConfigs = splatConfigs;
    for (const splatConfig of splatConfigs) {
      console.log('handle config', splatConfig);
      if (splatConfig.rendererScale !== 1) {
        this.setRenderScale(container, splatConfig.rendererScale, wrapper);
      }

      // resize
      resizeEventListener(
        this.camera,
        this.renderer,
        container,
        splatConfig.rendererScale,
      );
      await this.loadMesh(splatConfig);
      const viewerOptions = {
        sphericalHarmonicsDegree:
          splatConfig?.sphericalHarmonicsDegree >= 0
            ? splatConfig.sphericalHarmonicsDegree
            : 2,
        sharedMemoryForWorkers: false,
        renderer: this.renderer,
        camera: this.camera,
        useBuiltInControls: false,
        threeScene: this.scene,
        selfDrivenMode: false,
        ...splatConfig?.gaussianSplats3DOption,
      };

      viewerOptions.rootElement = container;
      console.log('viewerOptions', viewerOptions);
      this.viewer = new GaussianSplats3D.Viewer(viewerOptions);

      console.log('addSplatScene', splatConfig.url, splatConfig);
      splatConfig.showLoadingUI = false;
      splatConfig.progressiveLoad = true;
      await this.viewer.addSplatScene(splatConfig.url, splatConfig);

      this.changeUrlState(splatConfig.url, true);
    }
  }

  async add(index) {
    await this.viewer.addSplatScene(this.splatConfigs[index].url, {
      progressiveLoad: true,
      position: this.splatConfigs[index].position,
      rotation: this.splatConfigs[index].rotation,
      scale: this.splatConfigs[index].scale,
      showLoadingUI: false,
    });
    this.changeUrlState(this.splatConfigs[index].url, true);
    this.viewer.update();
    this.viewer.render();
    return this.splatConfigs;
  }

  remove(index) {
    let splatIndex = -1;
    for (let i = 0; i <= index && i < this.splatConfigs.length; i++) {
      if (this.splatConfigs[i].show === true) {
        splatIndex += 1;
      }
    }
    this.viewer.removeSplatScene(splatIndex, true);
    this.changeUrlState(this.splatConfigs[index].url, false);
  }

  changeUrlState(path, show) {
    const urlItem = this.splatConfigs.find(item => item.url === path);
    urlItem.show = show;
  }

  showInfoPanel(visible) {
    console.log('model-controller', visible);
    this.viewer.showInfo = visible;
    const infoPanel = document.querySelector('.infoPanel');
    const appDiv = document.getElementById('app');
    this.viewer.infoPanel.show();
    if (visible) {
      infoPanel.style.display = '';
      appDiv.appendChild(infoPanel);
      infoPanel.style.marginTop = '100px';
    } else {
      infoPanel.style.display = 'none';
    }
  }

  animate() {
    this.viewer?.update();
    this.viewer?.render();
  }
  onWindowResize() {}
}
