      threeContainer.value.addEventListener("selectstart", preventLongPress);

      // ç¦ç”¨æ‹–æ‹½å¼€å§‹
      threeContainer.value.addEventListener("dragstart", preventLongPress);

      // ç§»åŠ¨ç«¯é•¿æŒ‰äº‹ä»¶å¤„ç†
      let longPressTimer = null;

      threeContainer.value.addEventListener("touchstart", (e) => {
        longPressTimer = setTimeout(() => {
          e.preventDefault();
          e.stopPropagation();
        }, 500); // 500msåè§¦å‘é•¿æŒ‰é˜»æ­¢
      });

      threeContainer.value.addEventListener("touchend", () => {
        if (longPressTimer) {
          clearTimeout(longPressTimer);
          longPressTimer = null;
        }
      });

      threeContainer.value.addEventListener("touchmove", () => {
        if (longPressTimer) {
          clearTimeout(longPressTimer);
          longPressTimer = null;
        }
      });
    }
  } catch (error) {
    console.error("MetaverseViewer åˆå§‹åŒ–å¤±è´¥:", error);
  }
});

