import { MobileController } from './mobile-controller.js';

const W = 'w';
const A = 'a';
const S = 's';
const D = 'd';
const DIRECTIONS = [W, A, S, D];

export class InputController {
  constructor(container) {
    this.keysPressed = {};
    this.characterControls = null;
    this.mobileController = new MobileController(container);
    this.container = container;
    this.isMouseHidden = false;
    this.isOrbiting = false; // ç¯ç»•çŠ¶æ€æ ‡è®°
    this.mouseHideTimer = null; // é¼ æ ‡éšè—å®šæ—¶å™¨
    this.mouseHideDelay = 3000; // 3ç§’åéšè—é¼ æ ‡
    this.isMousePressed = false; // é¼ æ ‡æ˜¯å¦æ­£åœ¨è¢«æŒ‰ä¸‹
    this.orbitButton = null; // ç¯ç»•æŒ‰é’®
    this.onOrbitStart = null; // ç¯ç»•å¼€å§‹å›è°ƒ
    this.onOrbitEnd = null; // ç¯ç»•ç»“æŸå›è°ƒ
    this.initEventListeners();
    this.createOrbitButton();
  }

  initEventListeners() {
    document.addEventListener('keydown', this.onKeyDown.bind(this), false);
    document.addEventListener('keyup', this.onKeyUp.bind(this), false);

    // PCç«¯é¼ æ ‡æ§åˆ¶
    if (!this.mobileController.isMobileDevice()) {
      this.container.addEventListener(
        'mousedown',
        this.onMouseDown.bind(this),
        false,
      );
      this.container.addEventListener(
        'mousemove',
        this.onMouseMove.bind(this),
        false,
      );
      this.container.addEventListener(
        'mouseup',
        this.onMouseUp.bind(this),
        false,
      );
      document.addEventListener('keydown', this.onEscapeKey.bind(this), false);

      // åˆå§‹åŒ–æ—¶å¯åŠ¨å®šæ—¶å™¨
      this.resetMouseHideTimer();
    }
  }

  onMouseDown(event) {
    // PCç«¯é¼ æ ‡æŒ‰ä¸‹æ—¶éšè—é¼ æ ‡å¹¶è®¾ç½®æŒ‰ä¸‹çŠ¶æ€
    if (!this.mobileController.isMobileDevice()) {
      this.isMousePressed = true;
      this.hideMouse();
    }
  }

  onMouseMove(event) {
    // PCç«¯é¼ æ ‡ç§»åŠ¨æ—¶çš„å¤„ç†
    if (!this.mobileController.isMobileDevice()) {
      // åªæœ‰åœ¨é¼ æ ‡æ²¡æœ‰è¢«æŒ‰ä¸‹æ—¶æ‰æ˜¾ç¤ºé¼ æ ‡
      if (!this.isMousePressed) {
        // å¦‚æœé¼ æ ‡å·²éšè—ï¼Œåˆ™æ˜¾ç¤ºé¼ æ ‡
        if (this.isMouseHidden) {
          this.showMouse();
        }

        // é‡ç½®å®šæ—¶å™¨
        this.resetMouseHideTimer();
      }
    }
  }

  onMouseUp(event) {
    // PCç«¯é¼ æ ‡é‡Šæ”¾æ—¶æ˜¾ç¤ºé¼ æ ‡å¹¶å¯åŠ¨å®šæ—¶å™¨
    if (!this.mobileController.isMobileDevice()) {
      this.isMousePressed = false;
      this.showMouse();
      this.resetMouseHideTimer();
    }
  }

  onEscapeKey(event) {
    // æŒ‰ESCé”®æ˜¾ç¤ºé¼ æ ‡
    if (event.key === 'Escape' && this.isMouseHidden) {
      this.showMouse();
    }
  }

  hideMouse() {
    this.container.style.cursor = 'none';
    this.isMouseHidden = true;
    // æ¸…é™¤å®šæ—¶å™¨
    this.clearMouseHideTimer();
  }

  showMouse() {
    this.container.style.cursor = 'default';
    this.isMouseHidden = false;
    // æ¸…é™¤å®šæ—¶å™¨
    this.clearMouseHideTimer();
  }

  // é‡ç½®é¼ æ ‡éšè—å®šæ—¶å™¨
  resetMouseHideTimer() {
    // æ¸…é™¤ç°æœ‰å®šæ—¶å™¨
    this.clearMouseHideTimer();

    // è®¾ç½®æ–°çš„å®šæ—¶å™¨
    this.mouseHideTimer = setTimeout(() => {
      if (!this.isMouseHidden) {
        this.hideMouse();
      }
    }, this.mouseHideDelay);
  }

  // æ¸…é™¤é¼ æ ‡éšè—å®šæ—¶å™¨
  clearMouseHideTimer() {
    if (this.mouseHideTimer) {
      clearTimeout(this.mouseHideTimer);
      this.mouseHideTimer = null;
    }
  }

  onKeyDown(event) {
    if (event.shiftKey && this.characterControls) {
      this.characterControls.switchRunToggle();
    } else {
      this.keysPressed[event.key.toLowerCase()] = true;
    }
  }

  onKeyUp(event) {
    this.keysPressed[event.key.toLowerCase()] = false;
  }

  setCharacterControls(characterControls) {
    this.characterControls = characterControls;
  }

  isDirectionPressed() {
    // ç¯ç»•æ—¶ç¦ç”¨æ‰€æœ‰ç§»åŠ¨è¾“å…¥
    if (this.isOrbiting) return false;

    // æ£€æŸ¥é”®ç›˜è¾“å…¥æˆ–ç§»åŠ¨ç«¯æ‘‡æ†è¾“å…¥
    const keyboardPressed = DIRECTIONS.some(key => this.keysPressed[key] === true);
    const mobileInput = this.mobileController.getMovementInput();
    const mobilePressed =      Math.abs(mobileInput.x) > 0.1 || Math.abs(mobileInput.y) > 0.1;

    return keyboardPressed || mobilePressed;
  }

  isKeyPressed(key) {
    return this.keysPressed[key] === true;
  }

  getMovementDirection() {
    // ç¯ç»•æ—¶ç¦ç”¨æ‰€æœ‰ç§»åŠ¨è¾“å…¥
    if (this.isOrbiting) {
      return {
        forward: false,
        backward: false,
        left: false,
        right: false,
        joystick: { x: 0, y: 0 },
      };
    }

    // ä¼˜å…ˆä½¿ç”¨ç§»åŠ¨ç«¯è¾“å…¥
    if (this.mobileController.isMobileDevice()) {
      const mobileInput = this.mobileController.getMovementInput();
      if (Math.abs(mobileInput.x) > 0.1 || Math.abs(mobileInput.y) > 0.1) {
        return {
          forward: mobileInput.y > 0.1,
          backward: mobileInput.y < -0.1,
          left: mobileInput.x < -0.1,
          right: mobileInput.x > 0.1,
          joystick: mobileInput,
        };
      }
    }

    // é”®ç›˜è¾“å…¥
    return {
      forward: this.keysPressed[W],
      backward: this.keysPressed[S],
      left: this.keysPressed[A],
      right: this.keysPressed[D],
    };
  }

  getDirectionOffset() {
    // ç¯ç»•æ—¶ç¦ç”¨æ‰€æœ‰ç§»åŠ¨è¾“å…¥
    if (this.isOrbiting) return 0;

    // ä¼˜å…ˆå¤„ç†ç§»åŠ¨ç«¯æ‘‡æ†è¾“å…¥
    if (this.mobileController.isMobileDevice()) {
      const mobileInput = this.mobileController.getMovementInput();
      if (Math.abs(mobileInput.x) > 0.1 || Math.abs(mobileInput.y) > 0.1) {
        return Math.atan2(-mobileInput.x, mobileInput.y);
      }
    }

    // é”®ç›˜è¾“å…¥çš„æ–¹å‘åç§»è®¡ç®—
    let directionOffset = 0; // w

    if (this.keysPressed[W]) {
      if (this.keysPressed[A]) {
        directionOffset = Math.PI / 4; // w+a
      } else if (this.keysPressed[D]) {
        directionOffset = -Math.PI / 4; // w+d
      }
    } else if (this.keysPressed[S]) {
      if (this.keysPressed[A]) {
        directionOffset = Math.PI / 4 + Math.PI / 2; // s+a
      } else if (this.keysPressed[D]) {
        directionOffset = -Math.PI / 4 - Math.PI / 2; // s+d
      } else {
        directionOffset = Math.PI; // s
      }
    } else if (this.keysPressed[A]) {
      directionOffset = Math.PI / 2; // a
    } else if (this.keysPressed[D]) {
      directionOffset = -Math.PI / 2; // d
    }

    return directionOffset;
  }

  // è·å–è·‘æ­¥çŠ¶æ€ï¼ˆç§»åŠ¨ç«¯æŒ‰é’®æˆ–é”®ç›˜Shiftï¼‰
  getRunToggleState() {
    if (this.mobileController.isMobileDevice()) {
      return this.mobileController.getRunState();
    }
    return false; // é”®ç›˜é€šè¿‡Shiftä¸´æ—¶åˆ‡æ¢ï¼Œè¿™é‡Œè¿”å›false
  }

  // è·å–ç¯ç»•çŠ¶æ€
  getOrbitState() {
    return this.mobileController.getOrbitState();
  }

  // è®¾ç½®ç¯ç»•å›è°ƒå‡½æ•°
  // åˆ›å»ºç¯ç»•æŒ‰é’®
  createOrbitButton() {
    const orbitButton = document.createElement('div');
    orbitButton.setAttribute('id', 'orbitButton');
    orbitButton.innerHTML = 'ç¯é¡¾';
    orbitButton.style.cssText = `
            position: fixed;
            bottom: 50px;
            right: 50px;
            width: 80px;
            height: 80px;
            background: rgba(255, 255, 255, 0.3);
            border: 2px solid rgba(255, 255, 255, 0.5);
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 16px;
            font-weight: bold;
            color: white;
            z-index: 1000;
            touch-action: none;
            user-select: none;
        `;

    this.container.parentNode.appendChild(orbitButton);
    this.orbitButton = orbitButton;

    this.addOrbitButtonEvents();
  }

  // æ·»åŠ ç¯ç»•æŒ‰é’®äº‹ä»¶
  addOrbitButtonEvents() {
    const handleStart = (e) => {
      e.preventDefault();
      this.isOrbiting = true;
      this.orbitButton.style.background = 'rgba(100, 255, 100, 0.6)';

      // è§¦å‘ç¯ç»•å¼€å§‹äº‹ä»¶
      if (this.onOrbitStart) {
        this.onOrbitStart();
      }
    };

    const handleEnd = (e) => {
      e.preventDefault();
      this.isOrbiting = false;
      this.orbitButton.style.background = 'rgba(255, 255, 255, 0.3)';

      // è§¦å‘ç¯ç»•ç»“æŸäº‹ä»¶
      if (this.onOrbitEnd) {
        this.onOrbitEnd();
      }
    };

    // ç§»åŠ¨ç«¯è§¦æ‘¸äº‹ä»¶
    this.orbitButton.addEventListener('touchstart', handleStart, {
      passive: false,
    });
    this.orbitButton.addEventListener('touchend', handleEnd, {
      passive: false,
    });
    this.orbitButton.addEventListener('touchcancel', handleEnd, {
      passive: false,
    });

    // PCç«¯é¼ æ ‡äº‹ä»¶
    this.orbitButton.addEventListener('mousedown', handleStart, {
      passive: false,
    });
    this.orbitButton.addEventListener('mouseup', handleEnd, {
      passive: false,
    });
    this.orbitButton.addEventListener('mouseleave', handleEnd, {
      passive: false,
    });
  }

  // è®¾ç½®ç¯ç»•å›è°ƒå‡½æ•°
  setOrbitCallbacks(onStart, onEnd) {
    this.onOrbitStart = onStart;
    this.onOrbitEnd = onEnd;
  }

  destroy() {
    document.removeEventListener('keydown', this.onKeyDown);
    document.removeEventListener('keyup', this.onKeyUp);

    // æ¸…ç†PCç«¯äº‹ä»¶ç›‘å¬å™¨
    if (!this.mobileController.isMobileDevice()) {
      this.container.removeEventListener('mousedown', this.onMouseDown);
      this.container.removeEventListener('mousemove', this.onMouseMove);
      this.container.removeEventListener('mouseup', this.onMouseUp);
      document.removeEventListener('keydown', this.onEscapeKey);
      // æ¸…ç†å®šæ—¶å™¨
      this.clearMouseHideTimer();
      // æ¢å¤é¼ æ ‡æ˜¾ç¤º
      this.showMouse();
    }

    // æ¸…ç†ç¯ç»•æŒ‰é’®
    if (this.orbitButton) {
      this.orbitButton.remove();
    }

    this.mobileController.destroy();
  }
}
