<template>
  <div id="app">
    <ModelList v-if="curHash === ''"/>
    <Metaverse v-if="curHash === '#/viewer'"/>
  </div>
</template>
<script setup>
import ModelList from './pages/ModelList.vue';
import Metaverse from './pages/Metaverse.vue';

import { ref, onMounted } from 'vue';
const curHash = ref(window.location.hash);
onMounted(async () => {
  console.log('window.location.hash=', window.location.hash);
  // ç›‘å¬ hash ä¸­ modelUrl çš„å˜åŒ–å¹¶åˆ·æ–°é¡µé¢
  let currentModelUrl = null;

  // ä» hash ä¸­æå– modelUrl å‚æ•°
  function getModelUrlFromHash() {
    const hash = window.location.hash;
    console.log('hash===', hash);
    if (hash) {
      curHash.value = hash.split('?')[0];
    }
    const urlParams = new URLSearchParams(hash.split('?')[1] || '');
    return urlParams.get('modelUrl');
  }

  // åˆå§‹åŒ–å½“å‰ modelUrl
  currentModelUrl = getModelUrlFromHash();

  // ç›‘å¬ hashchange äº‹ä»¶
  window.addEventListener('hashchange', function () {
    console.log('hashchange==');
    const newModelUrl = getModelUrlFromHash();
    console.log('hashchange newModelUrl', newModelUrl);
    // å¦‚æœ modelUrl å‘ç”Ÿäº†å˜åŒ–ï¼Œåˆ·æ–°é¡µé¢
    console.log('hashchange currentModelUrl', currentModelUrl);
    if (currentModelUrl !== newModelUrl && newModelUrl !== null) {
      console.log('ModelUrl changed from', currentModelUrl, 'to', newModelUrl, '- reloading page');
      window.location.reload();
    }
    if (newModelUrl === null && currentModelUrl === null) {
      window.location.reload();
    }
    currentModelUrl = newModelUrl;
  });
});
</script>
<style scoped>
#app {
  width: 100%;
  height: 100vh;
}
</style>
