let walls = [];

function createWalls(scene) {
  // æ¸…é™¤æ—§å¢™é¢
  walls.forEach(wall => scene.remove(wall));
  walls = [];

  const wallHeight = 20;
  const wallThickness = 0.2;
  const spacing = spaceSize / gridSize;
  const offset = spaceSize / 2 - spacing / 2;

  // Xè½´æ–¹å‘å¢™æè´¨ (çº¢è‰²)
  const xWallMaterial = new THREE.MeshStandardMaterial({
    color: 0xff0000,
    transparent: true,
    side: THREE.DoubleSide,
  });

  // Zè½´æ–¹å‘å¢™æè´¨ (è“è‰²)
  const zWallMaterial = new THREE.MeshStandardMaterial({
    color: 0x0000ff,
    transparent: true,
    side: THREE.DoubleSide,
  });

  // ç”ŸæˆXè½´æ–¹å‘çš„å¢™ (å‚ç›´äºZè½´)
  for (let i = 0; i < gridSize - 1; i++) {
    for (let j = 0; j < gridSize; j++) {
      const wall = new THREE.Mesh(
        new THREE.BoxGeometry(spaceSize, wallHeight, wallThickness),
        xWallMaterial,
      );
      wall.position.set(
        -offset + i * spacing,
        wallHeight / 2,
        -offset + j * spacing,
      );
      walls.push(wall);
      scene.add(wall);
    }
  }

  // ç”ŸæˆZè½´æ–¹å‘çš„å¢™ (å‚ç›´äºXè½´)
  for (let i = 0; i < gridSize; i++) {
    for (let j = 0; j < gridSize - 1; j++) {
      const wall = new THREE.Mesh(
        new THREE.BoxGeometry(wallThickness, wallHeight, spaceSize),
        zWallMaterial,
      );
      wall.position.set(
        -offset + i * spacing,
        wallHeight / 2,
        -offset + j * spacing,
      );
      walls.push(wall);
      scene.add(wall);
    }
  }

  console.log(`åˆ›å»º ${gridSize}x${gridSize} ç½‘æ ¼ï¼Œå…± ${walls.length} å µå¢™`);
}
