export class PerformanceController {
  constructor(isMobile = false) {
    // è®¾å¤‡æ€§èƒ½æ£€æµ‹ç›¸å…³å±æ€§
    this.isMobile = isMobile;
    this.devicePerformanceLevel = isMobile ? 'medium' : 'high'; // PCé»˜è®¤é«˜æ€§èƒ½ï¼Œç§»åŠ¨ç«¯é»˜è®¤ä¸­ç­‰æ€§èƒ½
    this.recommendedFrameRate = isMobile ? 90 : 120; // PCé»˜è®¤120fpsï¼Œç§»åŠ¨ç«¯é»˜è®¤90fps
    this.performanceThresholds = {
      high: 120,
      medium: 90,
      low: 60,
    };

    // å¸§ç‡ç›‘æ§ç›¸å…³å±æ€§
    this.frameCount = 0;
    this.lastFrameTime = Date.now();
    this.recentFrameRates = []; // å­˜å‚¨æœ€è¿‘çš„å¸§ç‡æ•°æ®
    this.frameRateCheckInterval = 1000; // æ¯ç§’æ£€æŸ¥ä¸€æ¬¡å¸§ç‡
    this.lastFrameRateCheck = Date.now();
    this.frameRateDropThreshold = 0.3; // 30%çš„å¸§ç‡ä¸‹é™é˜ˆå€¼
    this.enableLowFrameRateMode = false; // æ˜¯å¦å¯ç”¨ä½å¸§ç‡æ¨¡å¼çš„å¼€å…³
    this.lastRecoveryTime = 0; // ä¸Šæ¬¡æ¢å¤ä½å¸§ç‡æ¨¡å¼çš„æ—¶é—´
    this.recoveryProtectionDelay = 10000; // æ¢å¤å10ç§’å†…ä¸å†è¿›å…¥ä½å¸§ç‡æ¨¡å¼
    this.monitoringStartTime = 0; // å¼€å§‹ç›‘æµ‹çš„æ—¶é—´
    this.monitoringDelay = 10000; // è¿›å…¥åœºæ™¯å10ç§’å¼€å§‹ç›‘æµ‹
    this.isMonitoringEnabled = false; // æ˜¯å¦å¯ç”¨å¸§ç‡ç›‘æµ‹
  }

  // æ£€æµ‹è®¾å¤‡æ€§èƒ½
  detectDevicePerformance() {
    // PCè®¾å¤‡é»˜è®¤ä¸ºé«˜æ€§èƒ½ï¼Œæ— éœ€æ£€æµ‹
    if (!this.isMobile) {
      this.devicePerformanceLevel = 'high';
      this.recommendedFrameRate = this.performanceThresholds.high;
      console.log('PCè®¾å¤‡é»˜è®¤æ€§èƒ½ç­‰çº§ï¼šé«˜ï¼Œæ¨èå¸§ç‡ï¼š120fps');
      return;
    }

    // ç§»åŠ¨ç«¯è®¾å¤‡æ€§èƒ½æ£€æµ‹
    try {
      const canvas = document.createElement('canvas');
      const gl = canvas.getContext('webgl') || canvas.getContext('experimental-webgl');

      if (!gl) {
        this.devicePerformanceLevel = 'low';
        this.recommendedFrameRate = this.performanceThresholds.low;
        console.log('WebGLä¸æ”¯æŒï¼Œè®¾å¤‡æ€§èƒ½ç­‰çº§ï¼šä½');
        return;
      }

      // è·å–GPUä¿¡æ¯
      const debugInfo = gl.getExtension('WEBGL_debug_renderer_info');
      let gpuInfo = '';
      if (debugInfo) {
        gpuInfo = gl
          .getParameter(debugInfo.UNMASKED_RENDERER_WEBGL)
          .toLowerCase();
      }

      // è·å–è®¾å¤‡å†…å­˜ä¿¡æ¯ï¼ˆå¦‚æœå¯ç”¨ï¼‰
      const memory = navigator.deviceMemory || 4; // é»˜è®¤4GB

      // è·å–CPUæ ¸å¿ƒæ•°
      const cores = navigator.hardwareConcurrency || 4; // é»˜è®¤4æ ¸

      // æ€§èƒ½è¯„åˆ†ç®—æ³•
      let performanceScore = 0;

      // GPUè¯„åˆ†ï¼ˆåŸºäºå¸¸è§ç§»åŠ¨GPUï¼‰
      if (gpuInfo.includes('adreno')) {
        if (
          gpuInfo.includes('740')
          || gpuInfo.includes('730')
          || gpuInfo.includes('720')
        ) {
          performanceScore += 40; // é«˜ç«¯Adreno
        } else if (
          gpuInfo.includes('650')
          || gpuInfo.includes('640')
          || gpuInfo.includes('630')
        ) {
          performanceScore += 30; // ä¸­ç«¯Adreno
        } else {
          performanceScore += 20; // ä½ç«¯Adreno
        }
      } else if (gpuInfo.includes('mali')) {
        if (
          gpuInfo.includes('g78')
          || gpuInfo.includes('g77')
          || gpuInfo.includes('g76')
        ) {
          performanceScore += 35; // é«˜ç«¯Mali
        } else if (
          gpuInfo.includes('g72')
          || gpuInfo.includes('g71')
          || gpuInfo.includes('g52')
        ) {
          performanceScore += 25; // ä¸­ç«¯Mali
        } else {
          performanceScore += 15; // ä½ç«¯Mali
        }
      } else if (gpuInfo.includes('apple')) {
        performanceScore += 45; // Apple GPUé€šå¸¸æ€§èƒ½è¾ƒå¥½
      } else {
        performanceScore += 20; // æœªçŸ¥GPUï¼Œç»™ä¸­ç­‰åˆ†æ•°
      }

      // å†…å­˜è¯„åˆ†
      if (memory >= 8) {
        performanceScore += 30;
      } else if (memory >= 6) {
        performanceScore += 25;
      } else if (memory >= 4) {
        performanceScore += 20;
      } else {
        performanceScore += 10;
      }

      // CPUæ ¸å¿ƒæ•°è¯„åˆ†
      if (cores >= 8) {
        performanceScore += 20;
      } else if (cores >= 6) {
        performanceScore += 15;
      } else if (cores >= 4) {
        performanceScore += 10;
      } else {
        performanceScore += 5;
      }

      // æ ¹æ®æ€»åˆ†ç¡®å®šæ€§èƒ½ç­‰çº§
      if (performanceScore >= 70) {
        this.devicePerformanceLevel = 'high';
        this.recommendedFrameRate = this.performanceThresholds.high;
      } else if (performanceScore >= 40) {
        this.devicePerformanceLevel = 'medium';
        this.recommendedFrameRate = this.performanceThresholds.medium;
      } else {
        this.devicePerformanceLevel = 'low';
        this.recommendedFrameRate = this.performanceThresholds.low;
      }

      console.log('è®¾å¤‡æ€§èƒ½æ£€æµ‹å®Œæˆï¼š');
      console.log(`- è®¾å¤‡å¾—åˆ†: ${performanceScore}`);
      console.log(`- æ€§èƒ½ç­‰çº§: ${this.devicePerformanceLevel}`);
      console.log(`- æ¨èå¸§ç‡: ${this.recommendedFrameRate}fps`);
      console.log(`- æ€§èƒ½è¯„åˆ†: ${performanceScore}`);
      console.log(`- GPUä¿¡æ¯: ${gpuInfo}`);
      console.log(`- å†…å­˜: ${memory}GB`);
      console.log(`- CPUæ ¸å¿ƒ: ${cores}`);
    } catch (error) {
      console.warn('è®¾å¤‡æ€§èƒ½æ£€æµ‹å¤±è´¥ï¼Œä½¿ç”¨é»˜è®¤ä¸­ç­‰æ€§èƒ½è®¾ç½®:', error);
      this.devicePerformanceLevel = 'medium';
      this.recommendedFrameRate = this.performanceThresholds.medium;
    }
  }

  // å¼€å§‹å¸§ç‡ç›‘æµ‹
  startMonitoring() {
    this.monitoringStartTime = Date.now();
    this.isMonitoringEnabled = false; // åˆå§‹ä¸å¯ç”¨ï¼Œç­‰å¾…å»¶è¿Ÿæ—¶é—´
    console.log('å¸§ç‡ç›‘æµ‹å°†åœ¨10ç§’åå¼€å§‹');
  }

  // ç›‘æ§å¸§ç‡å˜åŒ–
  monitorFrameRate() {
    const currentTime = Date.now();
    
    // æ£€æŸ¥æ˜¯å¦åˆ°äº†å¼€å§‹ç›‘æµ‹çš„æ—¶é—´
    if (!this.isMonitoringEnabled && this.monitoringStartTime > 0) {
      if (currentTime - this.monitoringStartTime >= this.monitoringDelay) {
        this.isMonitoringEnabled = true;
        console.log('å¼€å§‹å¸§ç‡ç›‘æµ‹');
      } else {
        // è¿˜æ²¡åˆ°ç›‘æµ‹æ—¶é—´ï¼Œåªè®¡æ•°ä¸åˆ†æ
        this.frameCount = this.frameCount + 1;
        return;
      }
    }

    // å¦‚æœç›‘æµ‹æœªå¯ç”¨ï¼Œç›´æ¥è¿”å›
    if (!this.isMonitoringEnabled) {
      this.frameCount = this.frameCount + 1;
      return;
    }

    this.frameCount = this.frameCount + 1;

    // æ¯ç§’è®¡ç®—ä¸€æ¬¡å¸§ç‡
    if (currentTime - this.lastFrameRateCheck >= this.frameRateCheckInterval) {
      const fps = this.frameCount / ((currentTime - this.lastFrameRateCheck) / 1000);
      this.recentFrameRates.push({
        fps,
        timestamp: currentTime,
      });

      // åªä¿ç•™æœ€è¿‘4ç§’çš„æ•°æ®ï¼ˆç”¨äºè®¡ç®—1ç§’å’Œ3ç§’çš„å¹³å‡å€¼ï¼‰
      this.recentFrameRates = this.recentFrameRates.filter(data => currentTime - data.timestamp <= 4000);

      // æ£€æŸ¥æ˜¯å¦éœ€è¦å¯ç”¨ä½å¸§ç‡æ¨¡å¼
      this.checkFrameRateChange();

      // é‡ç½®è®¡æ•°å™¨
      this.frameCount = 0;
      this.lastFrameRateCheck = currentTime;
    }
  }

  // æ£€æŸ¥å¸§ç‡å˜åŒ–å¹¶å†³å®šæ˜¯å¦å¯ç”¨ä½å¸§ç‡æ¨¡å¼
  checkFrameRateChange() {
    if (this.recentFrameRates.length < 4) return; // éœ€è¦è‡³å°‘4ç§’çš„æ•°æ®

    const currentTime = Date.now();

    // è®¡ç®—æœ€è¿‘1ç§’çš„å¹³å‡å¸§ç‡
    const recent1SecData = this.recentFrameRates.filter(data => currentTime - data.timestamp <= 1000);
    const avg1Sec = recent1SecData.reduce((sum, data) => sum + data.fps, 0)
      / recent1SecData.length;

    // è®¡ç®—ä¹‹å‰3ç§’çš„å¹³å‡å¸§ç‡ï¼ˆæ’é™¤æœ€è¿‘1ç§’ï¼‰
    const previous3SecData = this.recentFrameRates.filter(data => currentTime - data.timestamp > 1000
      && currentTime - data.timestamp <= 4000);
    const avg3Sec = previous3SecData.reduce((sum, data) => sum + data.fps, 0)
      / previous3SecData.length;

    if (avg3Sec > 0) {
      const frameRateRatio = avg1Sec / avg3Sec;
      const frameRateDrop = 1 - frameRateRatio;

      // è®¡ç®—æ¨èå¸§ç‡çš„30%é˜ˆå€¼
      const recommendedFrameRateThreshold = this.recommendedFrameRate * (1 - this.frameRateDropThreshold);
      const isFrameRateBelowRecommended = avg1Sec < recommendedFrameRateThreshold;
      // å¦‚æœ3så¹³å‡å¸§ç‡å°äºæ¨èå¸§ç‡çš„30%
      const isAvg3SecBelowRecommended = avg3Sec < recommendedFrameRateThreshold;

      // æ£€æŸ¥æ˜¯å¦åœ¨æ¢å¤ä¿æŠ¤æœŸå†…
      const isInRecoveryProtection = currentTime - this.lastRecoveryTime < this.recoveryProtectionDelay;
      // console.log(`ç›‘æ§å¸§ç‡ï¼šå½“å‰1så†…å¹³å‡å¸§ç‡ä¸º:${avg1Sec},3så†…å¹³å‡å¸§ç‡ä¸º:${avg3Sec}ï¼Œæ˜¯å¦åœ¨æ¢å¤ä¿æŠ¤æœŸ:${isInRecoveryProtection},æ˜¯å¦å°äºæ¨èå¸§ç‡é˜ˆå€¼(${recommendedFrameRateThreshold})ï¼š${isAvg3SecBelowRecommended},å½“å‰1så¸§ç‡æ˜¯å¦å°äº3så¹³å‡å¸§ç‡é˜ˆå€¼${frameRateDrop}`);
      // å¦‚æœå¸§ç‡ä¸‹é™è¶…è¿‡30% æˆ–è€… ä½äºæ¨èå¸§ç‡çš„30% æˆ–è€… ä¸‰ç§’å¹³å‡å¸§ç‡ä½äºæ¨èå¸§ç‡çš„30%ï¼Œå¯ç”¨ä½å¸§ç‡æ¨¡å¼
      if (
        (frameRateDrop > this.frameRateDropThreshold
          || isFrameRateBelowRecommended
          || isAvg3SecBelowRecommended)
        && !isInRecoveryProtection
      ) {
        if (!this.enableLowFrameRateMode) {
          this.enableLowFrameRateMode = true;
          let reason = '';
          if (frameRateDrop > this.frameRateDropThreshold) {
            reason = `å¸§ç‡ä¸‹é™${(frameRateDrop * 100).toFixed(1)}%`;
          } else if (isFrameRateBelowRecommended) {
            reason = `å½“å‰å¸§ç‡ä½äºæ¨èå€¼${this.recommendedFrameRate}fpsçš„30%(${recommendedFrameRateThreshold.toFixed(1)}fps)`;
          } else if (isAvg3SecBelowRecommended) {
            reason = `ä¸‰ç§’å¹³å‡å¸§ç‡ä½äºæ¨èå€¼${this.recommendedFrameRate}fpsçš„30%(${recommendedFrameRateThreshold.toFixed(1)}fps)`;
          }
          console.log(`æ£€æµ‹åˆ°${reason}ï¼Œå¯ç”¨ä½å¸§ç‡æ¨¡å¼`);
          console.log(`æœ€è¿‘1ç§’å¹³å‡å¸§ç‡: ${avg1Sec.toFixed(1)}fpsï¼Œä¹‹å‰3ç§’å¹³å‡å¸§ç‡: ${avg3Sec.toFixed(1)}fps`);
          console.log(`è®¾å¤‡æ€§èƒ½ç­‰çº§: ${this.devicePerformanceLevel}ï¼Œæ¨èå¸§ç‡: ${this.recommendedFrameRate}fps`);
        }
      } else if (
        frameRateDrop < this.frameRateDropThreshold * 0.5
        && avg1Sec >= recommendedFrameRateThreshold * 1.2
        && avg3Sec >= recommendedFrameRateThreshold * 1.2
      ) {
        // å¦‚æœå¸§ç‡æ¢å¤åˆ°è¾ƒå¥½æ°´å¹³ï¼ˆ1ç§’å’Œ3ç§’å¹³å‡å¸§ç‡éƒ½è¶…è¿‡æ¨èå¸§ç‡30%é˜ˆå€¼çš„1.2å€ï¼‰ï¼Œç¦ç”¨ä½å¸§ç‡æ¨¡å¼
        if (this.enableLowFrameRateMode && !this.isUserEnablLowFrameRateMode) {
          this.enableLowFrameRateMode = false;
          this.lastRecoveryTime = currentTime; // è®°å½•æ¢å¤æ—¶é—´
          console.log('å¸§ç‡æ¢å¤æ­£å¸¸ï¼Œç¦ç”¨ä½å¸§ç‡æ¨¡å¼ï¼Œ10ç§’å†…ä¸å†è¿›å…¥ä½å¸§ç‡æ¨¡å¼');
          console.log(`æœ€è¿‘1ç§’å¹³å‡å¸§ç‡: ${avg1Sec.toFixed(1)}fpsï¼Œä¹‹å‰3ç§’å¹³å‡å¸§ç‡: ${avg3Sec.toFixed(1)}fps`);
        }
      } else if (
        isInRecoveryProtection
        && (frameRateDrop > this.frameRateDropThreshold
          || isFrameRateBelowRecommended
          || isAvg3SecBelowRecommended)
      ) {
        // åœ¨ä¿æŠ¤æœŸå†…æ£€æµ‹åˆ°æ€§èƒ½é—®é¢˜ï¼Œä½†ä¸è¿›å…¥ä½å¸§ç‡æ¨¡å¼
        console.log(`æ£€æµ‹åˆ°æ€§èƒ½é—®é¢˜ï¼Œä½†åœ¨æ¢å¤ä¿æŠ¤æœŸå†…(å‰©ä½™${((this.recoveryProtectionDelay - (currentTime - this.lastRecoveryTime)) / 1000).toFixed(1)}ç§’)ï¼Œæš‚ä¸è¿›å…¥ä½å¸§ç‡æ¨¡å¼`);
      }
    }
  }

  // è·å–æ€§èƒ½ç­‰çº§
  getPerformanceLevel() {
    return this.devicePerformanceLevel;
  }

  // è·å–æ¨èå¸§ç‡
  getRecommendedFrameRate() {
    return this.recommendedFrameRate;
  }

  // è·å–æ˜¯å¦å¯ç”¨ä½å¸§ç‡æ¨¡å¼
  isLowFrameRateMode() {
    return this.enableLowFrameRateMode;
  }

  // é‡ç½®ä½å¸§ç‡æ¨¡å¼
  resetLowFrameRateMode() {
    if (this.enableLowFrameRateMode) {
      this.lastRecoveryTime = Date.now(); // è®°å½•æ¢å¤æ—¶é—´
    }
    this.enableLowFrameRateMode = false;
    this.isUserEnablLowFrameRateMode = false;
  }

  // æ‰‹åŠ¨è¿›å…¥ä½å¸§ç‡æ¨¡å¼
  enterLowFrameRateMode() {
    if (!this.enableLowFrameRateMode) {
      this.enableLowFrameRateMode = true;
      this.isUserEnablLowFrameRateMode = true;
      console.log('æ‰‹åŠ¨è¿›å…¥ä½å¸§ç‡æ¨¡å¼');
      console.log(`è®¾å¤‡æ€§èƒ½ç­‰çº§: ${this.devicePerformanceLevel}ï¼Œæ¨èå¸§ç‡: ${this.recommendedFrameRate}fps`);
    }
  }
}
