      const geometry = new THREE.BoxGeometry(...item.geometry);
      const material = new THREE.MeshBasicMaterial({
        color: 0x00ff00,
        transparent: true,
        opacity: 0.0, // å®Œå…¨é€æ˜
        // visible: false, // ä¸å¯è§
      });
      const zone = new THREE.Mesh(geometry, material);
      zone.position.set(...item.geometryPos);
      this.interactionZone.push(zone);
      const box = new THREE.Box3().setFromObject(zone);
      this.interactionBox.push(box);
      this.sceneController.getScene().add(zone);
      this.addModel(new THREE.Vector3(
        Number(item.geometryPos[0]) + item.modelPos[0],
        Number(item.geometryPos[1]) + item.modelPos[1],
        Number(item.geometryPos[2]) + item.modelPos[2],
      ), item.model, item.scale || [1, 1, 1], degreesToRadians(item.rotation || [0, 0, 0]));
