  // ä¸ºlabubuæ¨¡å‹æ·»åŠ ç¯å¢ƒå…‰æº
  addPointLightsForLabubu() {
    const ambientLight = new THREE.AmbientLight(0xffffcc, 0.6);
    // æ·»åŠ åˆ°åœºæ™¯
    this.sceneController.getScene().add(ambientLight);

    // å­˜å‚¨å¹³è¡Œå…‰å¼•ç”¨
    this.modelAmbientLight = ambientLight;
  }

  animate() {
    // ç›‘æ§å¸§ç‡å˜åŒ–
    this.performanceController.monitorFrameRate();
    this.debugPanel.animate();
    this.animationId = requestAnimationFrame(this.animate.bind(this));
    // åªæœ‰åœ¨å¯ç”¨ä½å¸§ç‡æ¨¡å¼æ—¶æ‰æ£€æŸ¥æ˜¯å¦éœ€è¦æš‚åœæ¸²æŸ“
    if (this.performanceController.isLowFrameRateMode()) {
      console.log('è¿›å…¥ä½å¸§ç‡æ¨¡å¼');
      const currentTime = Date.now();
      // 3s ååœæ­¢æ¸²æŸ“
      if (currentTime - this.lastInputTime > this.renderPauseDelay) {
        if (!this.isRenderingPaused) {
          this.playerController.saveCurrentPosition(
            this.sceneController,
            'animate',
          );
          this.isRenderingPaused = true;
          this.pauseStartTime = currentTime; // è®°å½•è¿›å…¥ä¸æ¸²æŸ“æ¨¡å¼çš„æ—¶é—´
          console.log('è¿›å…¥ä¸æ¸²æŸ“æ¨¡å¼');
        }

        // æ£€æŸ¥æ˜¯å¦éœ€è¦è‡ªåŠ¨æ¢å¤æ¸²æŸ“ï¼ˆ2ç§’åï¼‰
        if (currentTime - this.pauseStartTime > this.autoResumeDelay) {
          this.lastInputTime = currentTime; // é‡ç½®è¾“å…¥æ—¶é—´
          console.log('2ç§’åè‡ªåŠ¨æ¢å¤æ¸²æŸ“');
          this.resetRenderState();
        } else {
          return; // ç›´æ¥è¿”å›ï¼Œä¸æ‰§è¡Œä»»ä½•æ¸²æŸ“æ“ä½œ
        }
      }
    }

    const delta = this.clock.getDelta();
    // æ¸²æŸ“åœºæ™¯
    this.sceneController.animate();
    // æ›´æ–°ç©å®¶
    if (this.playerController && this.playerController.isLoaded()) {
      this.playerController.update(
        delta,
        this.inputController,
        this.sceneController.getCamera(),
      );

      // æ›´æ–°åœºæ™¯æ‘„åƒæœº
      const playerPosition = this.playerController.getPosition();
      this.sceneController.updateCamera(playerPosition);

      // æ£€æµ‹ç©å®¶æ˜¯å¦åœ¨äº¤äº’åŒºåŸŸå†…å¹¶æ›´æ–°å¯¹è¯æ¡†ä½ç½®
      this.checkInteractionZone(playerPosition);
    }

    // æ›´æ–°åŠ¨ç”»æ··åˆå™¨
    if (this.modelMixer) {
      this.modelMixer.update(delta);
    }

    // æ›´æ–°è½¨é“æ§åˆ¶å™¨
    this.sceneController.getOrbitControl().update();
  }

  // æ£€æµ‹ç©å®¶æ˜¯å¦åœ¨äº¤äº’åŒºåŸŸå†…
